<template>
  <div id="receiptDataTable">
    <el-row class="el-row-header" style="background-color: rgb(229,241,245)">
      <el-col :span="6" style="margin-left: 19px"></el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6"></el-col>
      <el-col :span="4"></el-col>
    </el-row>
    <template v-if="tableData.length===0">
      <el-row style="text-align: center">
        <img :src="imgUrl.default_0" style="width: 200px;margin-top: 15px">
      </el-row>
      <el-row style="text-align: center;font-size: 18px;color: #959697">
        <span></span>
      </el-row>
    </template>
    <template v-else>
    <template v-for="(item,index) in showData">
      <div>
        <el-row class="dataTable">
          <el-row class="el-row-header">
            <el-col :span="8" style="margin-left: 19px;">{{item.repoCertNo | nullSituation}}</el-col>
          </el-row>
          <el-row class="el-row-content">
            <el-col :span="6" style="margin-left: 19px;">
              <el-row>{{item.productName}}</el-row>
              <el-row>{{item.productQuantity}}</el-row>
            </el-col>
            <el-col :span="6">
              <el-row>{{item.repoEnterpriseName}}</el-row>
            </el-col>
            <el-col :span="6">
              <el-row>{{item.repoCertStatus | repoCertStatus}}</el-row>
            </el-col>
            <el-col :span="4">
              <el-button v-if="item.repoCertNo !== ''" size="small" @click.native.prevent="checkDetail(item.repoCertNo)"></el-button>
              <span v-else></span>
            </el-col>
          </el-row>
        </el-row>
      </div>
    </template>
    <el-pagination
      layout="total,prev, pager, next,jumper"
      @current-change="currentChange"
      :total="tableData.length" :page-size="pageSize1">
    </el-pagination>
    </template>
  </div>
</template>

<script>
  import store from '../../vuex/store'
  import constantData from '../../../../common/const'
  import default_0 from  '../../assets/default_0.png'

  export default {
    name: 'receiptDataTable',
    props: ['receiptList','status','pageSize'],
    data(){
      return{
        tableData:this.receiptList,
        showData:[],
        receiptStatus:this.status,
        imgUrl: {
          default_0:default_0
        }
      }
    },
    mounted(){/**/
      console.log(this.receiptList);
      this.getDataByStatus();
      this.getDataByPageNum(0);
    },
    watch:{
      receiptList(curVal){
        this.tableData=curVal;
        this.getDataByStatus();
        this.getDataByPageNum(0);
      }
    },
    computed: {
      state () {
        return store.state;
      },
      constantData () {
        return constantData;
      },
      pageSize1 () {
        return this.pageSize;
      }
    },
    methods:{
      currentChange(val){
        this.getDataByPageNum(val - 1)
      },
      getDataByStatus(){/*Tab*/
        if(this.repoStatus == 0){
          return
        }
        var res=[];
        for(var i=0;i<this.tableData.length;i++ ){
          var item = this.tableData[i];
          if(item.status == this.repoStatus){
            res.push(item)
          }
        }
        this.tableData = res;
      },
      getDataByPageNum(pageNum){
        if((pageNum + 1) * this.pageSize > this.tableData.length){
          this.showData = this.tableData.slice(pageNum * this.pageSize);
        }else {
          this.showData = this.tableData.slice(pageNum * this.pageSize,(pageNum + 1)*this.pageSize);
        }
      },
      checkDetail (checkId) {
        store.commit('setCheckIdRepoCert',checkId);
        this.$router.push('/warehousingCompany/receiptDetails');
      },
    }
  }
</script>



