/**/
Vue.filter('payingMethod',function(value){
  var readableStatus;
  switch(value){
    case 0:readableStatus="";break;
    case 1:readableStatus="";break;
    default:break;
  }
  return readableStatus;
})
/**/
Vue.filter('timeTransfer',function(value){
  console.log(value);
  if(value===''||value===0||value===''){
    return ""
  }
  return new Date(value).toLocaleString('chinese',{hour12:false}).replace(/\//g, "-");
})
/**/
Vue.filter('nullSituation',function(value){
  if(value===''||value===0){
    return "";
  }
  return value;
})

/**/
Vue.filter('numTransfer',function(value){
  var value=Math.round(parseFloat(value)*100)/100;
  var xsd=value.toString().split(".");
  if(xsd.length==1){
    value=value.toString()+".00";
    return value;
  }
  if(xsd.length>1){
    if(xsd[1].length<2){
      value=value.toString()+"0";
    }
    return value;
  }
})



