<template>
  <div>
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">
        <use xlink:href="#icon-locate"></use>
        
      </svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/logisticsCompany/companyAllBill'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row class="el-row-header statePosition">
        <el-col class="sellerColor stateShow">
          <svg class="icon detailIcon" aria-hidden="true">
            <use xlink:href="#icon-zhuangtai"></use>
            
          </svg>
          {{logisticsDetail.waybillStatusCode | wayBillStatus}}
          &nbsp;
          <el-button size="small" v-if="logisticsDetail.waybillStatusCode===constantData.SENDFORRESPONSE"
                     @click.native.prevent="sendConfirm(logisticsDetail.orderNo)">
          </el-button>
          &nbsp;<el-button size="small" v-if="logisticsDetail.waybillStatusCode===constantData.SENDED"
                           @click.native.prevent="receiveConfirm(logisticsDetail.orderNo)">
        </el-button>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">
                <use xlink:href="#icon-wl_H"></use>
                
              </svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="6" class="msgName keynote">{{logisticsDetail.wayBillNo | nullSituation}}</el-col>
                <!--<el-col :span="6" class="msgName">{{logisticsDetail.logisticsEnterpriseName}}</el-col>-->
                <el-col :span="6" class="msgName">
                  {{logisticsDetail.sendReqTime | timeTransfer | nullSituation}}
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName"></el-col>
              </el-row>

              <el-row class="collapseTop">
                <template v-for="(item,index) in logisticsDetail.operationRecordVo">
                  <el-row class="status-list"
                          :class="{circleColor:index==(logisticsDetail.operationRecordVo.length-1)}">
                    <el-col :span="8" :class="{circleColor1:index==(logisticsDetail.operationRecordVo.length-1)}"><span>{{item.operateTime | timeTransfer}} {{item.state | wayBillStatus}}</span>
                    </el-col>
                  </el-row>
                </template>
              </el-row>

            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">
                <use xlink:href="#icon-qita"></use>
                
              </svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row class="msgName keynote"></el-row>
              <el-row class="cutoff">
                <el-col :span="6" class="msgName">{{logisticsDetail.wayBillNo | nullSituation}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.senderRepoEnterpriseName}}</el-col>
                <el-col :span="10" class="msgName">{{logisticsDetail.senderRepoCertNo}}</el-col>
              </el-row>
              <el-row class="msgName keynote" style="margin-top: 10px"></el-row>
              <el-row class="cutoff">
                <el-col :span="6" class="msgName">{{logisticsDetail.receiverEnterpriseName}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.receiverRepoEnterpriseName}}</el-col>
              </el-row>
              <el-row class="msgName keynote" style="margin-top: 10px"></el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{logisticsDetail.productName}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.productQuantity}}</el-col>
                <el-col :span="6" class="msgName">(){{logisticsDetail.productPrice | numTransfer}}</el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>

</template>
<script>
  import store from '../../vuex/store'
  import constantData from '../../../../common/const'
  export default {
    name: 'index',
    data () {
      return {
        logisticsDetail: {
          productPrice: '',
          inRepoTime: '',
          sendReqTime: ''
        },
        confirmSend: {
          orderNo: '',
        },
      }
    },
    computed: {
      constantData () {
        return constantData;
      }
    },
    methods: {
      sendConfirm (checkId) {
        store.commit('setCheckIdOrder', checkId);
        this.confirmSend.orderNo = store.state.checkIdOrder;
        console.log(this.confirmSend);
        this.$http.post("../v1/waybill/confirmedWaybill", this.confirmSend, {emulateJSON: true}).then(
          function (res) {
            if (res.body.code != 0) {
              this.$message.error(res.body.message);
              return;
            }
            this.$message.success("");
            //2sdetail
            var _this=this;
            setTimeout(function () {
              _this.getDetail();
            }, 2000);
          },
          function (err) {
            console.log(err)
          }
        );

      },
      receiveConfirm (checkId) {
        store.commit('setCheckIdOrder', checkId);
        this.$http.put("../v1/waybill/receivedStatus?orderNo=" + store.state.checkIdOrder).then(
          function (res) {
            if (res.body.code != 0) {
              this.$message.error(res.body.message);
              return;
            }
            this.$message.success("");
            //detail
            var _this=this;
            setTimeout(function () {
              _this.getDetail();
            }, 2000);
          },
          function (err) {
            console.log(err);
          }
        );
      },
      getDetail () {
        //        
        this.$http.get("../v1/waybill/wayBillDetail?orderNo=" + store.state.checkIdOrder).then(function (res) {
          if (res.body.code != 0) {
            this.$message.error(res.body.message);
            return;
          }
          this.logisticsDetail = res.body.data;
          this.logisticsDetail.productPrice = (this.logisticsDetail.productQuantity === '' || this.logisticsDetail.productQuantity === 0) ? '' : this.logisticsDetail.productValue / this.logisticsDetail.productQuantity;
          this.logisticsDetail.sendReqTime = '';
          for (var item in this.logisticsDetail.operationRecordVo) {
            var temp = this.logisticsDetail.operationRecordVo[item];
            if (temp.state === constantData.SENDFORRESPONSE) {/**/
              this.logisticsDetail.sendReqTime = temp.operateTime;
              break;
            }
          }
        }, function (err) {
          console.log(err);
        });
      }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;

      this.getDetail();
    }
  }
</script>



