<template>
  <div id="detail" class="receiveDetails"  >
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-row class="el-row-header statePosition">
          <el-col class="detail_title_color stateShow"><svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-zhuangtai"></use></svg>
            {{detailInfo.detailVoList[0].status | receStatus}}</el-col>
          <el-col class="dotipRow"><span class="doTip">{{currentStatusInfo.statusSubTitle}}</span>&nbsp;<el-button size="small" v-if="currentStatusInfo.isShowHandleBtn" @click="handle()">{{currentStatusInfo.btnTitle}}</el-button></el-col>
        </el-row>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-yszk_hui"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="8" class="msgName keynote">{{detailInfo.detailVoList[0].receivableNo}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].isseDt | timeTransfer}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].dueDt | timeTransfer}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyeeEnterpriseName}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyerEnterpriseName}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].isseAmt}}</el-col>
                <el-col :span="8" class="msgName">(%){{detailInfo.detailVoList[0].rate}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">:</el-col>
              </el-row>
                <el-row class="collapseTop">
                  <template v-for="(item,index) in detailInfo.serialVoList">
                    <el-row class="status-list" :class="{circleColor:index==(detailInfo.serialVoList.length-1)}">
                      <el-col :span="24" :class="{circleColor1:index==(detailInfo.serialVoList.length-1)}"><span>{{item.time | timeTransfer}} {{item.receivableStatus | receAction}}</span></el-col>
                    </el-row>
                  </template>
                </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-qita"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row class="msgName keynote"></el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyeeEnterpriseName}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyeeAcctSvcrName}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyee}}</el-col>
              </el-row>
              <el-row class="cutoff">
                <!--<el-col :span="6" class="msgName">{{detailInfo.detailVoList[0].pyeeLinkMan | nullSituation}}</el-col>-->
                <el-col :span="8" class="msgName"></el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyeeLinkPhone | nullSituation}}</el-col>
              </el-row>
              <el-row class="msgName keynote" style="margin-top: 10px"></el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyerEnterpriseName}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyerAcctSvcrName}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyer}}</el-col>
              </el-row>
              <el-row class="cutoff">
                <!--<el-col :span="6" class="msgName">{{detailInfo.detailVoList[0].pyerLinkMan | nullSituation}}</el-col>-->
                <el-col :span="8" class="msgName"></el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].pyerLinkPhone | nullSituation}}</el-col>
              </el-row>
              <el-row class="msgName keynote" style="margin-top: 10px"></el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].orderNo}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].contractNo | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].invoiceNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].logisticsEnterpriseName | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].waybillNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].payeeRepoEnterpriseName | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].payeeRepoCertNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].payerRepoEnterpriseName | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{detailInfo.detailVoList[0].payerRepoCertNo | nullSituation}}</el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
  import Store from '../../vuex/store'
  import constantData from '../../../../common/const'
  import LocalStore from '../../../../common/store'

  export default {
    name:'detail',
    mounted:function () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      this.getDetail();
    },
    computed:{
      state () {
        return Store.state;
      }
    },
    data () {
      return {
        currentStatusInfo:{
          statusSubTitle:'',
          btnTitle:'',
          isShowHandleBtn:false
        },
        detailInfo:{
          detailVoList:[{
            receivableNo:'',
            orderNo:'',
            isseDt:'',
            dueDt:'',
            pyeeEnterpriseName:'',
            pyerEnterpriseName:'',
            isseAmt:'',
            rate:'',
            pyee:'',
            pyeeAcctSvcrName:'',
            pyeeLinkman:'',
            pyeePhone:'',
            pyer:'',
            pyerAcctSvcrName:'',
            pyerLinkMan:'',
            pyerPhone:'',
            contractNo:'',
            invoiceNo:''
          }
          ],
          serialVoList: [{
          }]
        }
      }
    },
    methods:{
      getDetail(){
          var detailParam = {
            receivableNo:Store.state.checkIdRece,
            operatorAcctId:LocalStore.fetchUserInfo().acctIds
          }
          this.$http.post('../v1/receivable/receivableInfoWithSerial',detailParam,{emulateJSON:true}).then((res) => {
            var data =  res.body.data;
            if(res.body.code != 0){
              this.$message.error(res.body.message);
              return;
            }
            //
            this.detailInfo = data;
            this.setStatusInfo(data);
          },(err) => {
            console.log(err);
          })
        },
      handle(){
        var status = this.detailInfo.detailVoList[0].status;
        var isBuyer = Store.state.isBuyer;

        if(isBuyer == 'true' && status == constantData.FORACCEPT){ //
          this.accept();
        }else if(isBuyer == 'true' && status == constantData.ACCEPTED){//
          this.cash();
        }else if(isBuyer == 'false' && status == constantData.ACCEPTED){//
          //
          this.$router.push("/allAccounts/detail/discount");
        }
      },
      accept(){
        var detailInfo = this.detailInfo.detailVoList[0];
        var acceptParam = {
          receivableNo:detailInfo.receivableNo, //
          replyerAcctId:LocalStore.fetchUserInfo().acctIds,//
          response:0       // 0. 1.
        }
        this.$http.post('../v1/receivable/accept',acceptParam,{emulateJSON:true}).then((res) => {
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }
          this.$message.success("");

          var _this=this;
          setTimeout(function () {
            _this.getDetail();
          }, 2000);
        },(err) => {
          console.log(err);
        })
      },
      cash(){
        var detailInfo = this.detailInfo.detailVoList[0];
        var cashParam = {
          receivableNo:detailInfo.receivableNo, //
          cashedAmount:detailInfo.isseAmt,      //
          response:0                            // 0. 1.
        }
        this.$http.post('../v1/receivable/cash',cashParam,{emulateJSON:true}).then((res) => {
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }
          this.$message.success("");
          this.currentStatusInfo.isShowHandleBtn = false

          var _this=this;
          setTimeout(function () {
            _this.getDetail();
          }, 2000);
        },(err) => {
          console.log(err);
        })
      },
      /**
      *  
      **/
      setStatusInfo(data){
        var status = data.detailVoList[0].status;
        var isBuyer = Store.state.isBuyer;

        if(status == constantData.FINISH){ //
          this.currentStatusInfo.statusSubTitle = ''
          return
        }
        if(status == constantData.DISCOUNTED){ //
          this.currentStatusInfo.statusSubTitle = ''
          return
        }

        if(isBuyer == 'false'){
          this.currentStatusInfo.statusSubTitle = ''
        }else {
          this.currentStatusInfo.statusSubTitle = ''
        }
        if(isBuyer == 'true' && status == constantData.FORACCEPT){
          this.currentStatusInfo.statusSubTitle = ','
          this.currentStatusInfo.btnTitle = ''
          this.currentStatusInfo.isShowHandleBtn = true
        }else if(isBuyer == 'true' && status == constantData.ACCEPTED){
          this.currentStatusInfo.statusSubTitle = ','
          this.currentStatusInfo.btnTitle = ''
          this.currentStatusInfo.isShowHandleBtn = true
        }else if(isBuyer == 'false' && status == constantData.ACCEPTED){
          this.currentStatusInfo.statusSubTitle = ','
          this.currentStatusInfo.btnTitle = ''
          this.currentStatusInfo.isShowHandleBtn = true
        }
      }
    }
  }
</script>
<style>
</style>



