<template>
  <div>
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row class="el-row-header statePosition">
        <el-col class="detail_title_color stateShow"><svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-zhuangtai"></use></svg>{{repoDetails.curRepoBusiStatus | repoStatus}}
          &nbsp;&nbsp;<el-button size="small" v-if="handleInfo.showHandleBtn"  @click.native.prevent="handle" >{{handleInfo.title}}</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-cc_H"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="8" class="msgName keynote">{{repoDetails.repoBusiNo}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.curRepoBusiStatus | repoStatus}}</el-col><!---->
                <el-col :span="8" class="msgName" v-if="repoDetails.curRepoBusiStatus < constantData.OUTFORRESPONSE">{{repoDetails.inRepoTime | timeTransfer}}</el-col>
                <el-col :span="8" class="msgName" v-else>{{repoDetails.outRepoTime | timeTransfer}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName keynote">{{repoDetails.repoCertNo | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.storeEnterpriseName}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.productName}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.productQuantity}}</el-col>
                <el-col :span="8" class="msgName">(){{repoDetails.productTotalPrice}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.outLogisticsEntepsName | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.outWaybillNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.inLogisticsEntepsName | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.inWaybillNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName"></el-col>
              </el-row>
              <el-row class="collapseTop">
                <template v-for="(item,index) in repoDetails.operationRecordVoList">
                  <el-row class="status-list" :class="{circleColor:index==(repoDetails.operationRecordVoList.length-1)}">
                    <el-col :span="8" :class="{circleColor1:index==(repoDetails.operationRecordVoList.length-1)}"><span>{{item.operateTime | timeTransfer}} {{item.state | repoStatus}}</span></el-col>
                  </el-row>
                </template>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
  import Store from '../../vuex/store'
  import constantData from '../../../../common/const'
  export default {
    name:'index',
    data () {
      return {
        handleInfo:{
          showHandleBtn:false,
          title:''
        },
        repoDetails:{
          curRepoBusiStatus:'',
          serialNumber:'20170403123456',
          state:'',
          storageTime:'2017-08-01',
          operationRecordVoList:[],
          inRepoTime:'',
          outRepoTime:''
        }
      }
    },
    computed:{
      state () {
        return Store.state
      },
      constantData () {
        return constantData
      }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      this.getDetail();
    },
    methods:{
      getDetail(){
        this.$http.get("../v1/repository/getRepoBusiHistoryList?repoBusinessNo="+Store.state.checkIdRepo).then(function(res){
//            
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }

          this.repoDetails=res.body.data;
          this.repoDetails.inRepoTime='';//
          this.repoDetails.outRepoTime='';
          for(var item in this.repoDetails.operationRecordVoList){
            var temp = this.repoDetails.operationRecordVoList[item];
            if(temp.state===constantData.ALREADYIN){/**/
              this.repoDetails.inRepoTime=temp.operateTime;
            }
            if(temp.state===constantData.ALREADYOUT){/**/
              this.repoDetails.outRepoTime=temp.operateTime;
            }
          }

          this.handleInfo.showHandleBtn = false;
          switch (this.repoDetails.curRepoBusiStatus){
            case constantData.INFORRESPONSE:
              this.handleInfo.showHandleBtn = true;
              this.handleInfo.title = '';
              break;
            case constantData.FORIN:
              this.handleInfo.showHandleBtn = true;
              this.handleInfo.title = '';
              break;
            case constantData.FOROUT:
              this.handleInfo.showHandleBtn = true;
              this.handleInfo.title = '';
              break;
          }

        },function(err){
          console.log(err)
        });
      },
      handle(){
      switch (this.repoDetails.curRepoBusiStatus){
        case constantData.INFORRESPONSE:
            this.inResponse();
          break;
        case constantData.FORIN:
            this.inConfirm();
          break;
        case constantData.FOROUT:
            this.outConfirm();
          break;
        }
      },
      inResponse(){
        var param = {repoBusinessNo:Store.state.checkIdRepo}
        this.$http.post('../v1/repository/incomeApplyResponse',param,{emulateJSON:true}).then((res) => {
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }
          this.$message.success("");
          //2sdetail
          var _this=this;
          setTimeout(function () {
            _this.getDetail();
          }, 2000);
        },(err) => {
          console.log(err);
        })
      },
      inConfirm(){
        this.$http.put('../v1/repository/incomeConfirm?repoBusinessNo='+Store.state.checkIdRepo+'&orderNo='+Store.state.checkIdOrder).then((res) => {
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }
          this.$message.success("");
          //2sdetail
          var _this=this;
          setTimeout(function () {
            _this.getDetail();
          }, 2000);
        },(err) => {
          console.log(err);
        })
      },
      outConfirm(){
        this.$http.put('../v1/repository/outcomeConfirm?repoBusinessNo='+Store.state.checkIdRepo).then((res) => {
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }
          this.$message.success("");
          //2sdetail
          var _this=this;
          setTimeout(function () {
            _this.getDetail();
          }, 2000);
        },(err) => {
          console.log(err);
        })
      }
    }
  }
</script>



