<template>
  <div class="receiveDetails"  >
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
    <el-row class="el-row-header statePosition">
      <el-col class="detail_title_color sellerColor stateShow">
            <svg class="icon detailIcon" aria-hidden="true"><use xlink:href="#icon-zhuangtai"></use></svg>{{orderDetail.txDetail.operationRecordVoList[orderDetail.txDetail.operationRecordVoList.length-1].state | transactionStatus}}
            &nbsp;<el-button class="button_color" size="small" v-if="(state.isBuyer==='false')&&(orderDetail.txDetail.operationRecordVoList[orderDetail.txDetail.operationRecordVoList.length-1].state===constantData.UNCONFIRMED)" @click.native.prevent="confirmOrder(orderDetail.txDetail.orderId)"></el-button>
            <el-button size="small" v-if="(state.isBuyer==='false')&&(orderDetail.txDetail.operationRecordVoList[orderDetail.txDetail.operationRecordVoList.length-1].state===constantData.CONFIRMED)&&(orderDetail.receOver.receLatestStatus===constantData.NOMESSAGE)" @click.native.prevent="signBill(orderDetail.txDetail.orderId)"></el-button>
            <el-button size="small" v-if="(state.isBuyer==='false')&&(orderDetail.receOver.receLatestStatus===constantData.ACCEPTED)&&(orderDetail.wayBillOver.wayBillLatestStatus===constantData.FORSEND)" @click.native.prevent="sendGood(orderDetail.txDetail.orderId)"></el-button>
            <el-button size="small" v-if="(state.isBuyer==='true')&&(orderDetail.receOver.receLatestStatus===constantData.FORACCEPT)" @click.native.prevent="acceptBill(orderDetail.receOver.receNo)"></el-button>
      </el-col>
    </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-order_H"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="6" class="msgName keynote" v-if="state.isBuyer==='false'"></el-col>
                <el-col :span="6" class="msgName keynote" v-else></el-col>
              </el-row>
              <el-row class="">
                <el-col :span="6" class="msgName" v-if="state.isBuyer==='true'"> {{orderDetail.txDetail.payeeCompanyName}}</el-col>
                <el-col :span="6" class="msgName" v-else>{{orderDetail.txDetail.payeeCompanyName}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.payerBank}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.payerAccount}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.payingMethod | payingMethod}}</el-col>
              </el-row>
              <el-row style="margin-top: 10px;">
                <el-col :span="6" class="msgName keynote"></el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.orderId}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.operationRecordVoList[0].operateTime | timeTransfer}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.productName}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.productQuantity}}</el-col>
                <el-col :span="6" class="msgName">(){{orderDetail.txDetail.productTotalPrice | numTransfer}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{orderDetail.txDetail.operationRecordVoList[orderDetail.txDetail.operationRecordVoList.length-1].state | transactionStatus}}</el-col>
                <el-col :span="6" class="collapseBtn"><i class="el-icon-caret-bottom" @click="orderCollapse" :class="{rotate:isOrderCollapse, rotate1:!isOrderCollapse}"></i></el-col>
              </el-row>
              <!--<el-row class="">-->
              <!--<el-col :span="8" v-if="(orderDetail.txDetail.orderState!==constantData.UNCONFIRMED)">{{orderDetail.txDetail.orderConfirmTime | timeTransfer}}</el-col>-->
              <!--</el-row>-->
                <el-row v-show="isOrderCollapse" class="collapseTop">
                  <template v-for="(item,index) in orderDetail.txDetail.operationRecordVoList">
                    <el-row class="status-list" :class="{circleColor:index==(orderDetail.txDetail.operationRecordVoList.length-1)}">
                      <el-col :span="8" :class="{circleColor1:index==(orderDetail.txDetail.operationRecordVoList.length-1)}"><span>{{item.operateTime | timeTransfer}} {{item.state | transactionStatus}}</span></el-col>
                    </el-row>
                  </template>
                </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-yszk_hui"></use></svg>
              <span class="keynote"></span><el-button size="mini" type="text" class="detailButton" v-if="(orderDetail.receOver.receLatestStatus!==constantData.NOMESSAGE)" @click="receDetailPage(orderDetail.receOver.receNo)"> ></el-button>
            </div>
            <div class="box-card mycard1 detailContent" v-if="orderDetail.receOver.receLatestStatus===constantData.NOMESSAGE">
              <span class="msgName">!</span>
            </div>
            <div class="box-card mycrad1 detailContent" v-else-if="orderDetail.receOver.receLatestStatus===constantData.FORISSUE">
              <span class="msgName">!</span>
            </div>
            <div class="box-card mycard1 detailContent" v-else>
              <el-row>
                <el-col :span="6" class="msgName keynote">{{orderDetail.receOver.receNo}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.receOver.receGenerateTime | timeTransfer}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.receOver.dueDate | timeTransfer}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{orderDetail.receOver.receivingSide}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.receOver.payingSide}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">(){{orderDetail.receOver.receAmount | numTransfer}}</el-col>
                <el-col :span="6" class="msgName">{{orderDetail.receOver.coupon}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">
                  {{orderDetail.receOver.receLatestStatus | receStatus}}
                </el-col>
                <el-col :span="6" class="collapseBtn"><i class="el-icon-caret-bottom" @click="receCollapse" :class="{rotate:isReceCollapse, rotate1:!isReceCollapse}"></i></el-col>
              </el-row>

              <el-row v-show="isReceCollapse" class="collapseTop">
                <template v-for="(item,index) in receHistory">
                  <el-row class="status-list" :class="{circleColor:index==(receHistory.length-1)}">
                    <el-col :span="8" :class="{circleColor1:index==(receHistory.length-1)}"><span>{{item.time | timeTransfer}} {{item.receivableStatus | receStatus}}</span></el-col>
                  </el-row>
                </template>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
      <el-col :span="24">
        <el-card class="box-card mybox" style="width:100%">
          <div slot="header" class="clearfix el-row-header">
            <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-wl_H"></use></svg>
            <span class="keynote"></span><el-button size="mini" type="text" class="detailButton" @click="logisticsDetailPage(orderDetail.txDetail.orderId)" v-if="(orderDetail.wayBillOver.wayBillLatestStatus!==constantData.NOMESSAGE)&&(orderDetail.wayBillOver.wayBillLatestStatus!==constantData.FORSEND)"> ></el-button>
          </div>
          <div class="box-card mycard1 detailContent" v-if="orderDetail.wayBillOver.wayBillLatestStatus===constantData.NOMESSAGE">
            <span class="msgName"></span>
          </div>
          <div class="box-card mycard1 detailContent" v-else-if="orderDetail.wayBillOver.wayBillLatestStatus===constantData.FORSEND">
            <span class="msgName"></span>
          </div>
          <div class="box-card mycard1 detailContent" v-else>
            <el-row>
              <el-col :span="6" class="msgName keynote">{{wayBillHistory.wayBillNo}}</el-col>
              <el-col :span="6" class="msgName">{{wayBillHistory.sendReqTime | timeTransfer}}</el-col>
            </el-row>
            <el-row class="">
              <el-col :span="6" class="msgName">{{wayBillHistory.logisticsEnterpriseName}}</el-col>
            </el-row>
            <el-row>
              <el-col :span="8" class="msgName">
                {{orderDetail.wayBillOver.wayBillLatestStatus | wayBillStatus}}
              </el-col>
              <el-col :span="6" class="collapseBtn"><i class="el-icon-caret-bottom" @click="wayBillCollapse" :class="{rotate:isWayBillCollapse, rotate1:!isWayBillCollapse}"></i></el-col>
            </el-row>
            <el-row v-show="isWayBillCollapse" class="collapseTop">
              <template v-for="(item,index) in wayBillHistory.operationRecordVo">
                <el-row class="status-list" :class="{circleColor:index==(wayBillHistory.operationRecordVo.length-1)}">
                  <el-col :span="8" :class="{circleColor1:index==(wayBillHistory.operationRecordVo.length-1)}"><span>{{item.operateTime | timeTransfer}} {{item.state | wayBillStatus}}</span></el-col>
                </el-row>
              </template>
            </el-row>
          </div>
        </el-card>
      </el-col>
    </el-row>
      <el-row>
      <el-col :span="24">
        <el-card class="box-card mybox" style="width:100%">
          <div slot="header" class="clearfix el-row-header" v-if="state.isBuyer==='true'">
            <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-cc_H"></use></svg>
            <span class="keynote"></span><el-button size="mini" type="text" class="detailButton" v-if="orderDetail.repoOver.payerRepoBusiState>=constantData.INFORRESPONSE"  @click="repoDetailPage(orderDetail.repoOver.payerRepoBusinessNo)"> ></el-button>
          </div>
          <div slot="header" class="clearfix el-row-header" v-else>
            <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-cc_H"></use></svg>
            <span class="keynote"></span><el-button size="mini" type="text" class="detailButton" v-if="orderDetail.repoOver.payeeRepoBusiState>=constantData.FOROUT" @click="repoDetailPage(orderDetail.repoOver.payeeRepoBusinessNo)">  ></el-button>
          </div>
          <div class="box-card mycard1 detailContent" v-if="state.isBuyer==='true'"><!---->
            <div v-if="orderDetail.repoOver.payerRepoBusiState<constantData.INFORRESPONSE"><!---->
              <span class="msgName"></span>
              <!---->
            </div>
            <div v-else>
            <el-row>
              <el-col :span="6" class="msgName keynote">{{orderDetail.repoOver.payerRepoBusinessNo}}</el-col>
              <el-col :span="6" class="msgName">{{orderDetail.repoOver.inApplyTime | timeTransfer}}</el-col>
            </el-row>
            <el-row>
              <el-col :span="6" class="msgName">{{orderDetail.txDetail.payerRepo}}</el-col>
            </el-row>
            <el-row>
              <el-col :span="6" class="msgName">{{orderDetail.repoOver.payerRepoBusiState | repoStatus}}</el-col>
              <el-col :span="6" class="collapseBtn"><i class="el-icon-caret-bottom" @click="buyerRepoCollapse" :class="{rotate:isBuyerRepoCollapse, rotate1:!isBuyerRepoCollapse}"></i></el-col>
            </el-row>
              <el-row v-show="isBuyerRepoCollapse" class="collapseTop">
                <template v-for="(item,index) in buyerRepoHistory">
                  <el-row class="status-list" :class="{circleColor:index==(buyerRepoHistory.length-1)}">
                    <el-col :span="8" :class="{circleColor1:index==(buyerRepoHistory.length-1)}"><span>{{item.operateTime | timeTransfer}} {{item.state | repoStatus}}</span></el-col>
                  </el-row>
                </template>
              </el-row>
            </div>
          </div>
          <div class="box-card mycard1 detailContent" v-if="state.isBuyer==='false'">
            <div v-if="orderDetail.repoOver.payeeRepoBusiState<constantData.FOROUT">
              <span class="msgName"></span>
              <!---->
            </div>
            <div v-else>
            <el-row>
              <el-col :span="6" class="msgName keynote">{{orderDetail.repoOver.payeeRepoBusinessNo}}</el-col>
              <el-col :span="6" class="msgName">{{orderDetail.repoOver.payeeRepoCertNo}}</el-col>
              <el-col :span="6" class="msgName" v-if="orderDetail.repoOver.payeeRepoBusiState>=constantData.CONFIRMED">{{orderDetail.repoOver.outApplyTime | timeTransfer}}</el-col>
            </el-row>
            <el-row>
              <el-col :span="6" class="msgName" v-if="orderDetail.txDetail.payeeRepo===''"></el-col>
              <el-col :span="6" class="msgName" v-else>{{orderDetail.txDetail.payeeRepo}}</el-col>
              <!---->
              <el-col :span="6" class="msgName">{{orderDetail.txDetail.payeeCompanyName}}</el-col>
            </el-row>
            <el-row>
              <el-col :span="8" class="msgName">{{orderDetail.repoOver.payeeRepoBusiState | repoStatus}}</el-col>
              <el-col :span="6" class="collapseBtn"><i class="el-icon-caret-bottom" @click="buyeeRepoCollapse" :class="{rotate:isBuyeeRepoCollapse, rotate1:!isBuyeeRepoCollapse}"></i></el-col>
            </el-row>
              <el-row v-show="isBuyeeRepoCollapse" class="collapseTop">
                <template v-for="(item,index) in buyeeRepoHistory">
                  <el-row class="status-list" :class="{circleColor:index==(buyeeRepoHistory.length-1)}">
                    <el-col :span="8" :class="{circleColor1:index==(buyeeRepoHistory.length-1)}"><span>{{item.operateTime | timeTransfer}} {{item.state | repoStatus}}</span></el-col>
                  </el-row>
                </template>
              </el-row>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    </el-card>
  </div>
</template>
<script>
  import store from '../../vuex/store'
  import constantData from '../../../../common/const'
  import userInfo from '../../../../common/store'
  export default {
    name: 'index',
    data () {
      return {
        orderDetail: {
          txDetail: {
            operationRecordVoList:[
              {state:'',operateTime:''}
            ],
          },
          receOver: {},
          repoOver: {},
          wayBillOver: {}
        },
        receHistory:[
        ],
        wayBillHistory:{
          operationRecordVo:[],
          sendReqTime:''
        },
        buyerRepoHistory:[
        ],
        buyeeRepoHistory:[
        ],
        isOrderCollapse:false,
        isReceCollapse:false,
        isWayBillCollapse:false,
        isBuyerRepoCollapse:false,
        isBuyeeRepoCollapse:false,
      }
    },
    computed: {
      state () {
        return store.state;
      },
      constantData () {
          return constantData;
      }
    },
    methods: {
      confirmOrder (checkId) {
        console.log("");
        store.commit('setCheckIdOrder',checkId);
        this.$router.push("/order/confirmOrder");
      },
      signBill (checkId) {
        console.log("");
        store.commit('setCheckIdOrder',checkId);
        this.$router.push("/allAccounts/signout/signout");
      },
      sendGood (checkId) {
        console.log("");
        store.commit('setCheckIdOrder',checkId);
        this.$router.push("/logistics/deliver");
      },
      acceptBill (checkId) {
        console.log("");
        store.commit('setCheckIdRece',checkId);
        this.$router.push("/allAccounts/accept/accept");
      },
      orderCollapse () {
          if(this.isOrderCollapse){
            this.isOrderCollapse=false;
          }
          else {
            this.isOrderCollapse=true;
            this.isReceCollapse=false;
            this.isWayBillCollapse=false;
            this.isBuyerRepoCollapse=false;
            this.isBuyeeRepoCollapse=false;
            console.log("");
          }
      },
      receCollapse () {
        if(this.isReceCollapse){
          this.isReceCollapse=false;
        }
        else {
          this.isOrderCollapse=false;
          this.isReceCollapse=true;
          this.isWayBillCollapse=false;
          this.isBuyerRepoCollapse=false;
          this.isBuyeeRepoCollapse=false;
          console.log("");

          /*this.$http.get("http://localhost/server_test/getData4.php").then(function(res){
              this.receHistory=res.body.data;
          },
          function(err){
             console.log(err);
          });*/
        }
      },
      wayBillCollapse () {
        if(this.isWayBillCollapse){
          this.isWayBillCollapse=false;
        }
        else {
          this.isOrderCollapse=false;
          this.isReceCollapse=false;
          this.isWayBillCollapse=true;
          this.isBuyerRepoCollapse=false;
          this.isBuyeeRepoCollapse=false;
          console.log("");
        }
      },
      buyerRepoCollapse () {
        if(this.isBuyerRepoCollapse){
          this.isBuyerRepoCollapse=false;
        }
        else {
          this.isOrderCollapse=false;
          this.isReceCollapse=false;
          this.isWayBillCollapse=false;
          this.isBuyerRepoCollapse=true;
          this.isBuyeeRepoCollapse=false;
          console.log("");
        }
      },
      buyeeRepoCollapse () {
        if(this.isBuyeeRepoCollapse){
          this.isBuyeeRepoCollapse=false;
        }
        else {
          this.isOrderCollapse=false;
          this.isReceCollapse=false;
          this.isWayBillCollapse=false;
          this.isBuyerRepoCollapse=false;
          this.isBuyeeRepoCollapse=true;
          console.log("");
        }
      },
      receDetailPage (checkId) {
          store.commit('setCheckIdRece',checkId);
        this.$router.push("/allAccounts/detail/detail");
      },
      logisticsDetailPage(checkId){
          store.commit('setCheckIdOrder',checkId);
        this.$router.push("/logistics/wayBillDetails");
      },
      repoDetailPage(checkId){
        store.commit('setCheckIdRepo',checkId);
        this.$router.push("/warehousing/repoDetails");
      }
    },
    mounted() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      console.log("the state checkIdOrder is:" + store.state.checkIdOrder);
      this.$http.get("../v1/order/detail?orderNo=" + store.state.checkIdOrder).then(
        function (res) {
          // 
          if(res.body.code != 0){
            this.$message.error(res.body.message);
            return;
          }
          this.orderDetail = res.body.data;
//          
          if(this.orderDetail.receOver.receNo!==''){
              var receTemp={};
              receTemp.receivableNo=this.orderDetail.receOver.receNo;
              receTemp.operatorAcctId=userInfo.fetchUserInfo().acctIds;//localStorage
              this.$http.post("../v1/receivable/receivableInfoWithSerial",receTemp,{emulateJSON:true}).then(function(res){
                if(res.body.code != 0){
                  this.$message.error(res.body.message);
                  return;
                }
                console.log(""+res.body.data);
                this.receHistory=res.body.data.serialVoList;//
              },function(err){
                  console.log(err);
              });
          }
//          
          if(store.state.isBuyer==='true'){
          if(this.orderDetail.repoOver.payerRepoBusinessNo!==''){
              this.$http.get("../v1/repository/getRepoBusiHistoryList?repoBusinessNo="+this.orderDetail.repoOver.payerRepoBusinessNo).then(function(res){
                if(res.body.code != 0){
                  this.$message.error(res.body.message);
                  return;
                }
                  console.log(""+res.body.data);
                  this.buyerRepoHistory=res.body.data.operationRecordVoList;
              },function (err) {
                console.log(err);
              });
          }
          }
          else {
              if(this.orderDetail.repoOver.payeeRepoBusinessNo!==''){
                this.$http.get("../v1/repository/getRepoBusiHistoryList?repoBusinessNo="+this.orderDetail.repoOver.payeeRepoBusinessNo).then(function(res){
                  if(res.body.code != 0){
                    this.$message.error(res.body.message);
                    return;
                  }
                  console.log(""+res.body.data);
                  this.buyeeRepoHistory=res.body.data.operationRecordVoList;
                },function (err) {
                  console.log(err);
                });
              }
          }
//          
          if(this.orderDetail.wayBillOver.wayBillLatestStatus>0){
              this.$http.get("../v1/waybill/wayBillDetail?orderNo="+this.orderDetail.txDetail.orderId).then(function(res){
                if(res.body.code != 0){
                  this.$message.error(res.body.message);
                  return;
                }
                console.log(""+res.body.data);
                this.wayBillHistory=res.body.data;
                this.wayBillHistory.sendReqTime='';
                for(var item in this.wayBillHistory.operationRecordVo){
                  var temp=this.wayBillHistory.operationRecordVo[item];
                  if(temp.state===constantData.SENDFORRESPONSE){/**/
                    this.wayBillHistory.sendReqTime=temp.operateTime;
                    break;
                  }
                }
              },function(err){
                  console.log(err);
              });
          }
        }, function (res) {
          // 
          console.log(res);
        }
      );
    }
  }
</script>
<style>

</style>



